.item1 {
  grid-column: 1 / -1;
  grid-row: 1 / 1;
  text-align: center;
}

/* Pie Chart */
.item2 {
  grid-column: 5 / 10;
  grid-row: 1 / 8;
  z-index: 1;
}

.minipiechart {
  width: 100%;
  max-width: 100px;
  grid-column: 8 / 9;
  grid-row: 8 / 10;
}

/* UK Map */
.item3 {
  grid-column: 1 / 9;
  grid-row: 1 / 17;
  /* align-self: stretch; */
  //z-index: 100;
}

/* Small UK Map */
/* .smallukmap {
  grid-column: 5 / 7;
  grid-row: 8 / 12;
} */

/* Line Chart 1 */
.item4 {
  grid-column: 10 / -1;
  grid-row: 2 / 7;
}
.item5 {
  grid-column: 10 / -1;
  grid-row: 7 / 12;
}

.item6 {
  grid-column: 10 / -1;
  grid-row: 12 / -1;
}

